.c {
   height: 100vh;
   position: relative;
}

.c-bg {
   width: 2rem;
   height: 100%;
   background-color: #fdf0d5;
   position: absolute;
}

.c-wrapper {
   padding: 50px;
   display: flex;
}

.c-left {
   flex: 1;
}

.c-right {
   flex: 1;
   display: flex;
   justify-content: center;
   flex-direction: column;
}

.c-title {
   font-size: 60px;
   width: 80%;
}

.c-info-item {
   display: flex;
   align-items: center;
   margin: 50px 0px;
   font-weight: 300;
   width: 70%;
}

.c-icon {
   width: 40px;
   height: 40px;
   margin-right: 15px;
   margin-left: 5px;
}

.c-icon-email {
   width: 40px;
   height: 40px;
   margin-right: 15px;
   margin-left: 5px;
}

.c-desc {
   font-weight: 200;
}

form {
   margin-top: 20px;
}

input {
   width: 75%;
   height: 50px;
   border: none;
   border-bottom: 1px solid black;
   margin: 10px 0px;
   font-size: 14px;
   padding: 5px;
}

textarea {
   width: 100%;
   margin: 10px 0px;
   font-size: 14px;
   padding: 5px;
}

button {
   border: none;
   padding: 15px;
   background-color: #023e8a;
   color: white;
   font-weight: 500;
   cursor: pointer;
}

.c-confirm {
   width: 100%;
   margin: 10px 0px;
   font-weight: 300;
}

@media screen and (max-width: 480px) {
   .c-wrapper {
      height: 80%;
      flex-direction: column;
      justify-content: space-between;
      padding: 30px 40px;
   }

   .c-left {
      display: flex;
      flex-direction: column;
      justify-content: center;
   }

   .c-title {
      font-size: 35px;
      width: 100%;
   }

   .c-info-item {
      margin: 20px 0px;
      width: 100%;
   }

   .c-icon {
      height: 25px;
      width: 25px;
   }

   .c-icon-email {
      height: 25px;
      width: 25px;
      margin-right: 15px;
      margin-left: 5px;
   }

   .c-desc {
      display: none;
   }

   form {
      display: flex;
      flex-direction: column;
      margin: 0;
   }

   input {
      width: 75%;
      height: 30px;
      margin: 5px 0px;
   }

   textarea {
      width: 95%;
   }

   .c-confirm {
      font-size: 12px;
   }
}

@media screen and (min-width: 481px) and (max-width: 768px) {
   .c-title {
      font-size: 40px;
   }

   .c-icon {
      height: 30px;
      width: 30px;
   }

   .c-icon-email {
      height: 30px;
      width: 30px;
      margin-left: 5px;
      margin-right: 15px;
   }

   form {
      margin: 0;
   }

   input {
      margin: 5px 0px;
   }

   textarea {
      height: 20%;
   }

   button {
      padding: 10px;
   }
}

@media screen and (min-width: 769px) and (max-width: 960px) {
   .c-wrapper {
      height: 90%;
      padding: 30px 40px;
   }

   .c-left {
      display: flex;
      flex-direction: column;
      justify-content: center;
   }

   .c-title {
      font-size: 50px;
   }

   .c-icon-email {
      margin-left: 6px;
      margin-right: 14px;
   }

   form {
      margin-top: 0;
   }

   input {
      margin: 5px 0px;
   }
}
